<template>
	<div class="login">
		<p class="tit">欢迎登录</p>
	<form v-if="!isreg">
		<div class="con">
			<label>
				<div class="name info">用户名：</div>
				<input type="text" v-model="name">
			</label>
		</div>
		<div class="con">
			<label>
				<div class="info pas">密 码：</div>
				<input type="password" v-model="password">
			</label>
		</div>
		<div class="btn">
			<button type="button" @click="login()">登录</button>
			<button type="button" @click="reg()">注册</button>
		</div>
	</form>
	<form v-else="">
		<div class="con">
			<label>
				<div class="name info">用户名：</div>
				<input type="text" v-model="name">
			</label>
		</div>
		<div class="con">
			<label>
				<div class="pas info">密 码：</div>
				<input type="password" v-model="password">
			</label>
		</div>
		<div class="con">
			<label>
				<div class="pas info">再次确认密码：</div>
				<input type="password" v-model="repeat">
			</label>
		</div>
		<div class="btn">
			<button class="btn1" type="button"  @click="adduser()">确定</button>
			<button type="button" class="btn2" @click="cancel()">取消</button>
		</div>
	</form>		
	</div>
</template>

<script>
	export default{
		name:'login',
		data() {
			return {
				isreg: false,
				name: '',
				password:'',
				repeat:'',
			}
		},
		methods:{
			
			login(){
				if (localStorage.getItem('name')===this.name && localStorage.getItem('password')===this.password) {
					this.name=''
					this.password=''
					this.$router.push('/home/list')
				} else{
					alert('用户名密码错误！')
				}
				
			},
			reg(){
				this.isreg =true
			},
			cancel(){
				this.isreg=false
			},
			adduser(){
				if (this.password===this.repeat) {
					localStorage.setItem('name',this.name)
					localStorage.setItem('password',this.password)	
					this.name =''
					this.password=''
					this.isreg=false
				} else{
					alert('两次密码不正确！')
				}
			

			}
			
			
			
		}
	}
</script>

<style lang="scss">
	.login{
		width: 640px;
		margin: 0 auto;
		padding: 0 10px;
	}
	.tit{
		font-family: 'Times New Roman', Times, serif;
		font-size: 16px;
		padding-left: 14px;
		height: 40px;
		line-height: 40px;
		font-weight: 800;
		text-align: center;
	}
	.con{
		display: flex;
		margin: 5px 0;
		justify-content:center;
		.info{
			height: 40px;
			line-height: 40px;
			font-size: 14px;
		}
		input{
			height: 30px;
			padding: 2px
		}
		
		
	}
	.btn{
		margin-top: 14px;
		display: flex;
		flex-flow: row nowrap;
		justify-content: space-around;
		button{
			background: #42b983;
			color: #fff;
			border: none;
			font-size: 16px;
			padding: 3px;
		}
		
	}
</style>
